import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';

Vue.use(Router);
export default new Router({
  mode: 'hash',
  base: process.env.NODE_ENV === 'development' ? '' : './dist',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
  ],
});
